  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  background-color: rgba(245, 234, 245, 0.404);
  transition: .1s;
  max-width: 100vw;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root {
  --blue: rgb(6, 6, 39);
  --white: #ffffff;
}
*{
  overflow-x: hidden;
}
